/**
 * A list of pivots.
 */
export class RequestPivot {
    /**
     * A key corresponding to a matching field facet label or stats.
     */
    key?: string;
    pivots?: RequestPivot[];

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
            if (input.pivots) {
                this.pivots = input.pivots.map((item: any) => {
                    return new RequestPivot(item);
                });
            }
        }
    }

}
